<template>
	<div>
		<div class="container">
        <div class="spacer">
            <div class="row">
                <div class="col-lg-12 col-sm-12 ">
                    <article>
                                                    
                            <div class="post-image">
                                                            </div>
                        
                        
                        
                        
                                            </article>
                </div>
            </div>
            <div class="row contact">
                <div class="col-lg-6 col-sm-6 ">

                    <h4 id="contact_div"><i class="fa fa-envelope"></i>       </h4>

                    <div id="sendmessage"><i class="fa fa-check-circle"></i>
                        &nbsp;          .  !</div>
                    <div id="errormessage">:   </div>

                    <form method="POST" action="http://smartfordesign.net/smartend/frontend2/contact" accept-charset="UTF-8" class="contactForm"><input name="_token" type="hidden" value="72If195fm7HERVq7b4ZUfFAXNreZuyPTBvRGAjrS">
                    <div class="form-group">
                        <input placeholder="" class="form-control" id="name" data-msg="  " data-rule="minlen:4" name="contact_name" type="text" value="">
                        <div class="alert alert-warning validation"></div>
                    </div>
                    <div class="form-group">
                        <input placeholder=" " class="form-control" id="email" data-msg="   " data-rule="email" name="contact_email" type="email" value="">
                        <div class="validation"></div>
                    </div>
                    <div class="form-group">
                        <input placeholder=" " class="form-control" id="phone" data-msg="    " data-rule="minlen:4" name="contact_phone" type="text" value="">
                        <div class="validation"></div>
                    </div>
                    <div class="form-group">
                        <input placeholder=" " class="form-control" id="subject" data-msg="   " data-rule="minlen:4" name="contact_subject" type="text" value="">
                        <div class="validation"></div>
                    </div>
                    <div class="form-group">
                        <textarea placeholder=" " class="form-control" id="message" rows="8" data-msg="   " data-rule="required" name="contact_message" cols="50"></textarea>
                        <div class="validation"></div>
                    </div>

                                      
                                        <div>
                        <button type="submit" class="btn btn-theme"> </button>
                    </div>
                    <br>
                    </form>

                </div>
                                    <div class="col-lg-6 col-sm-6 ">
